import React from 'react'
import ListItem from './ListItem'

const List = ({ listData, isShowLayer, showNoteDetail, changeStatusShow, onClick, addBtnClick }) => {
  let showDetailCls = showNoteDetail ? 'toLeft' : 'toRight'
  return (
    <div className={"left-box " + showDetailCls}>
      <h1></h1>
      <span title="" className="add-new" onClick={ () => addBtnClick(!isShowLayer) }><i className="iconfont">&#xe889;</i></span>
      <ul className="list-wrap">
        {listData.map((item) => {
          const cls = (item.isActive) ? 'active' : ''
          return (
            <ListItem key={item.id} id={item.id} title={item.title} cls={cls} showNoteDetail={showNoteDetail} changeStatusShow={changeStatusShow} clickPreview={onClick} />
          )
        })
        }
      </ul>
    </div>
  )
}

export default List


